  var apiUrl = "https://backend.amourette.no/api/";
  var apiUrlGroup = "https://backend.amourette.no/api/groups/";
  useEffect(() => {
    // Check if the username key exists in session storage
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = JSON.parse(storedUsername);
    console.log(userLogin);
    console.log("test");
    if (userLogin === null) {
      localStorage.removeItem("userLogin");
      localStorage.removeItem("userSearch");
      window.location.href = "/login";
    }
  }, []);
  useEffect(() => {
    const checkuser = async () => {
      const storedUsername = localStorage.getItem("userLogin");
      const userLogin = JSON.parse(storedUsername);
      let formData = {
        id: userLogin.id,
        email: userLogin.email,
      };
      try {
        const res = await axios.post(apiUrl + "getUserDetail", formData, {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        });

        setUserData(res.data.result);
      } catch (err) {
        // Enhanced error handling
        if (err.response) {
        } else if (err.request) {
          console.error("Request data:", err.request);
        } else {
          console.error("Error message:", err.message);
        }
      }
    };
    checkuser();
  }, []);
  useEffect(() => {
    const getAllMygroup = async () => {
      const storedUsername = localStorage.getItem("userLogin");
      const userLogin = JSON.parse(storedUsername);
      let formData = {
        user_id: userLogin.id,
      };
      try {
        const res = await axios.post(apiUrlGroup + "get_AllMygroup", formData, {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        });
        setgroups(res.data.result);
      } catch (err) {
        // Enhanced error handling
        if (err.response) {
        } else if (err.request) {
          console.error("Request data:", err.request);
        } else {
          console.error("Error message:", err.message);
        }
      }
    };
    getAllMygroup();
  }, []);
